*{
  box-sizing: border-box;
}
body{
  font-family: 'Roboto', sans-serif;
}
img{
  max-width: 100%;
}
.header {
  width: 100%;
  background: #000;
  padding: 15px;
}
.header .logo {
  display: flex;
  align-items: center;
  color: #fdd325 ;
  font-size: 28px;
  font-weight: bold;
  justify-content: center;
}
.header .logo img {
  width: 40px;
  max-width: 100%;
}
.form-wrapper {
  max-width: 550px;
  margin: 50px auto 20px;
  padding: 20px;
  box-shadow: 0px 0px 5px 1px #0000002e;
  border-radius: 10px;
}
.form-wrapper .input-name , .input-description{
  width: 100%;

}
.form-wrapper .input-wrapper ,.form-wrapper .btn-wrapper{
  margin-top: 20px;
}


.btn-mint {
  color: #000;
  background-color: #fdd325 !important;
  border-color: #fdd325 !important;
  margin-top: 2%;
  border: none;
  font-size: 20px;
  padding: 15px 35px;
  border-radius: 8px;
  cursor: pointer;
  font-weight: 500;
    font-family: 'Roboto', sans-serif;
}

.file-reader{
  margin-top: 2%;
}

.selected-image{
  margin-bottom: 20px;
  width: 100%;

}


.custom-file-input {
  color: transparent;
}
.custom-file-input::-webkit-file-upload-button {
  visibility: hidden;
}
.custom-file-input::before {
  content: 'Choose files';
  color: black;
  display: inline-block;
  background: #fdd325;
  border: 1px solid #fdd325;
  border-radius: 3px;
  padding: 7px 12px;
  outline: none;
  white-space: nowrap;
  cursor: pointer;
  text-shadow: 1px 1px #fdd325;
  font-weight: 500;
  font-size: 15px;
}
.custom-file-input:hover::before {
  border-color: black;
}
.custom-file-input:active {
  outline: 0;
}
.custom-file-input:active::before {
  background: -webkit-linear-gradient(top, #e3e3e3, #f9f9f9); 
}
